import gsap from 'gsap'

import BreadCrumb from '@/components/Blocks/BreadCrumb'
import SlideShow from '@/components/Blocks/SlideShow'
import MemberCard from '@/components/Card/MemberCard'

import functions from '@/compositions/functions'
const { loadImage } = functions()

export default {
    components: {
        BreadCrumb,
        SlideShow,
        MemberCard,
    },
    async asyncData ({ $config, $axios, error }) {
        try {
            const { data } = await $axios.$get(`${$config.APP_BACKEND_API}/wp-json/api/introduction`)

            const isOpen = {}

            data.fields.block.forEach((item, key) => {
                isOpen[`block_${key}`] = false
            })

            const seo = data.fields.seo

            return {
                data,
                isOpen,
                seo,
            }
        } catch (e) {
            console.log(e)
            error({ statusCode: 404, message: 'Post not found' })
            return {
                data: {},
                isOpen: {},
                seo: {},
            }
        }
    },
    data () {
        return {
            isDesktop: null,
        }
    },
    head () {
        return {
            title: this.seo.title + ' | ' + this.$config.APP_TITLE_TEMPLATE,
            meta: [
                { hid: 'description', name: 'description', content: this.seo.desc },
                { hid: 'og:title', property: 'og:title', content: this.seo.title },
                { hid: 'og:description', property: 'og:description', content: this.seo.desc },
                { hid: 'og:url', property: 'og:url', content: `${process.env.APP_URL}${this.$route.fullPath}` },
                { hid: 'og:site_name', property: 'og:site_name', content: this.seo.title },
                { hid: 'og:image', property: 'og:image', content: this.seo.thumb ? this.seo.thumb : `${process.env.APP_URL}/og_img.jpg` },
                { hid: 'og:image:alt', property: 'og:image:alt', content: this.seo.desc },
                { hid: 'twitter:description', name: 'twitter:description', content: this.seo.desc },
                { hid: 'twitter:title', name: 'twitter:title', content: this.seo.title },
                { hid: 'twitter:image', name: 'twitter:image', content: this.seo.thumb ? this.seo.thumb : `${process.env.APP_URL}/og_img.jpg` },
            ],
        }
    },
    mounted () {
        this.$store.dispatch('ADD_LOADING_STACK', loadImage())
        this.$store.dispatch('WAIT_LOADING')

        this.resize()
        window.addEventListener('resize', this.resize)
        window.addEventListener('scroll', this.scroll)

        this.gsapInit()
    },
    beforeDestroy () {
        window.removeEventListener('resize', this.resize)
        window.removeEventListener('scroll', this.scroll)
    },
    methods: {
        resize () {
            this.isDesktop = window.innerWidth >= 1200
        },
        scroll () {
            const introduction = document.querySelector('.introduction')
            const introductionBlock = document.querySelectorAll('.introduction__block')
            const teachers = this.$refs.teachers

            // if ((window.pageYOffset >= this.$refs.block_3[0].offsetTop && !this.isDesktop) && (this.isOpen.block_3 || this.isOpen.block_4 || this.isOpen.block_5)) {
            //     introduction.classList.add('-bg')
            // } else if ((window.pageYOffset >= this.$refs.block_3[0].offsetTop) && this.isDesktop) {
            //     introduction.classList.add('-bg')
            // } else {
            //     introduction.classList.remove('-bg')
            // }

            if (window.pageYOffset >= introductionBlock[2]?.offsetTop && this.isDesktop) {
                introduction.classList.add('-bg')
            } else if (window.pageYOffset >= introductionBlock[0]?.offsetTop / 3 && !this.isDesktop) {
                introduction.classList.add('-bg')
            } else {
                introduction.classList.remove('-bg')
            }

            if ((window.pageYOffset >= teachers.offsetTop - teachers.clientHeight + window.innerHeight * 0.2 && this.isDesktop)) {
                introduction.classList.add('-bg-darker')
            } else if ((window.pageYOffset >= teachers.offsetTop - window.innerHeight / 1.5 && !this.isDesktop)) {
                introduction.classList.add('-bg-darker')
            } else {
                introduction.classList.remove('-bg-darker')
            }
        },
        toggle (key) {
            this.isOpen[`block_${key}`] = !this.isOpen[`block_${key}`]
        },
        gsapInit () {
            gsap.fromTo('.breadcrumb', { y: -20, opacity: 0 }, { y: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.introduction__title', { x: 20, opacity: 0 }, { x: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.slide-show', { opacity: 0 }, { opacity: 1 }).delay(1)
            gsap.utils.toArray('.introduction__block').forEach((el) => {
                const tl = gsap.timeline({
                    scrollTrigger: {
                        trigger: el,
                        start: window.innerWidth >= 768 ? 'top bottom-=25%' : 'top bottom-=5%',
                        toggleActions: 'play none none reverse',
                    },
                })
                tl.fromTo(el, { marginTop: 30, opacity: 0 }, { marginTop: 0, opacity: 1 })
            })
            const tl = gsap.timeline({
                scrollTrigger: {
                    trigger: '.introduction__teachers-list',
                    start: window.innerWidth >= 768 ? 'top center' : 'top bottom-=15%',
                    toggleActions: 'play none none reverse',
                },
            })
            tl.fromTo('.introduction__teachers-item', { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 })
        },
    },
}
